import React from 'react';
import Standing from './Standing';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

export default class Leaderboard extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            loading: true,
            standings: [],
            cachedStandings: [],
            allStandingsVisible: false
        }

        fetch(this.props.GetRankingsUrl)
            .then(response => response.json())
            .then(jsonResult => {
                this.setState({
                    cachedStandings: jsonResult,
                    standings: this.state.standings.concat(jsonResult.slice(0, 10)),
                    loading: false
                })
            })
            .catch(e => console.log(e))
    }
    componentDidMount() {
        
        window.onscroll = () => {
            if (this.state.cachedStandings.length === 0 ||
                this.state.standings.length === 0 ||
                this.state.allStandingsVisible ||
                (window.innerHeight + document.documentElement.scrollTop) < document.documentElement.offsetHeight) {
                return;
            }

            this.setState({
                loading: true
            })

            let endRange = this.state.standings.length + 10
            let allStandingsVisible = false
            if (this.state.standings.length + 10 > this.state.cachedStandings) {
                endRange = this.state.cachedStandings.length
                allStandingsVisible = true
            }

            this.setState({
                standings: this.state.standings.concat(this.state.cachedStandings.slice(this.state.standings.length, endRange)),
                loading: false,
                allStandingsVisible: allStandingsVisible
            })
        }
    }
    render() {
        const placeIcons = [
            './img/LargeGold.png',
            './img/LargeSilver.png',
            './img/LargeCopper.png',
        ]
        const result = this.state.standings.map(x => {
            return <Standing
                key={x.place}
                Name={x.name}
                Place={x.place}
                PlaceImage={placeIcons[x.place - 1]}
                Score={x.score}
                Avatar={x.avatar}
                FreeCompany={x.freeCompany}
            />
        })

        return (
            <div>
                <Row className='mb-1 descriptor-bar'>
                    <Col sm={2} className='text-center'>Rank</Col>
                    <Col sm={4}>Name</Col>
                    <Col sm={2} className='text-center'>Free Company</Col>
                    <Col sm={2} className='text-right'>Score</Col>
                    <Col sm={2}></Col>
                </Row>
                {result}
                {this.state.loading && <div className='text-center'>Loading...</div>}
            </div>
        )
    }
}