import React from 'react';
import Card from 'react-bootstrap/Card';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';


export default class Standing extends React.Component {
    render() {
        return (
            <Card className='mb-1'>
                <Row>
                    <Col sm={2} className='text-center my-auto medal'>
                        {this.props.PlaceImage && <img src={this.props.PlaceImage} alt={this.props.Place} />}
                    </Col>
                    <Col sm={4} className='my-auto'>{this.props.Name}</Col>
                    <Col sm={2} className='my-auto text-center'>{this.props.FreeCompany}</Col>
                    <Col sm={2} className='my-auto text-right'>{this.props.Score}</Col>
                    <Col sm={2} className='my-auto'>
                        <img
                            className='float-right img-thumbnail'
                            src={this.props.Avatar}
                            alt={this.props.Name}
                            style={{ width: '96px', height: '96px' }}
                        />
                    </Col>
                </Row>
            </Card>
        )
    }
}